:root {
  --bg-color: #fffffe;
  --main-color: #6246ea;
  --sec-color: #d1d1e9;
  --tertiary-color: #e45858;
  --button-text: #fffffe;
  --text-color: #2b2c34;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  z-index: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--main-color);
  color: #212121;
  padding: 3em;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.search {
  z-index: 1;
  width: 80vw;
  max-width: 50em;
  background-color: var(--bg-color);
  border-radius: .7em;
  flex-direction: column;
  padding: 4em 3em;
  transition: all 1s cubic-bezier(.455, .03, .515, .955);
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 1.3em 1.9em rgba(8, 21, 65, .3);
}

.search > * {
  transition: all 1s cubic-bezier(.165, .84, .44, 1);
}

.search__box {
  border: none;
  border-bottom: 2px solid var(--main-color);
  color: var(--text-color);
  font-size: 1em;
  font: inherit;
  text-align: center;
  outline: none;
  padding: 0 .7em .6em;
}

.country-list {
  z-index: 2;
  max-height: 10em;
  border-inline: var(--main-color) 1px solid;
  list-style: none;
  overflow: scroll;
  box-shadow: inset -200px 200px 7px -200px rgba(0, 0, 0, .75);
}

.country-list__item {
  border-bottom: var(--sec-color) 1px solid;
  cursor: pointer;
  gap: 2em;
  padding: .3em;
  font-size: 1.2em;
  display: flex;
}

.country-list__item:hover {
  background-color: var(--sec-color);
}

.country-list__item:last-child {
  border-bottom-color: var(--main-color);
}

.country-info {
  overflow: scroll;
}

.country-info__symbols {
  justify-content: center;
  gap: 7em;
  margin-top: 2em;
  margin-bottom: 2em;
  display: flex;
}

.country-info__symbols > img {
  max-width: 20em;
  max-height: 10em;
}

.country-info__flag {
  border: .5px solid #000;
}

.country-info__common-name {
  text-align: center;
  letter-spacing: .1em;
  margin-top: .5em;
  margin-bottom: .5em;
  font-size: 2em;
}

.country-info__data__wrapper {
  align-items: baseline;
  gap: 1em;
  margin-bottom: .2em;
  display: flex;
}

.country-info__data__separator {
  width: 20em;
  border-bottom: var(--sec-color) 2px double;
  margin-top: .3em;
  margin-bottom: .5em;
}

